<template>
    <div>

        <progress-dlg v-if="progressDlg" :show="progressDlg" persistent>
            <v-card>
                <v-card-subtitle>Sending</v-card-subtitle>
                <v-card-text>
                    <v-progress-circular :size="70" :width="7" color="primary" indeterminate></v-progress-circular>
                </v-card-text>
            </v-card>
        </progress-dlg>

        <v-dialog transition="dialog-bottom-transition" v-model="mainDlg">
            <template>

                <v-card class="email-v-card">
                    <v-card-title>E-mail</v-card-title>
                    <v-card-subtitle class="email-subtitle">
                        <a href="mailto: office@daptools.cf">office@daptools.cf</a>
                    </v-card-subtitle>

                    <v-card-text>
                        <v-form ref="emailForm" v-model="valid" lazy-validation>
                            <v-text-field
                                v-model="name"
                                label="Name"
                            ></v-text-field>

                            <v-text-field
                                v-model="email"
                                :rules="emailRules"
                                label="E-mail"
                                required
                            ></v-text-field>

                            <v-text-field
                                v-model="subject"
                                :counter="256"
                                :rules="subjectRules"
                                label="Subject"
                            ></v-text-field>

                            <v-textarea
                                v-model="message"
                                label="Message"
                                hint="Share your thoughts with us"
                                rows=6
                                counter
                            ></v-textarea>
                        </v-form>
                    </v-card-text>

                    <v-card-actions class="justify-end">
                        <v-btn text color="error" @click="$emit('done')" rounded>Cancel</v-btn>
                        <v-btn text color="success" @click="sendEmail()" rounded>Send</v-btn>
                    </v-card-actions>
                </v-card>

            </template>
        </v-dialog>

    </div>
</template>

<script>

    import emailjs from 'emailjs-com';
    import secrets from '@/secrets.json';
    import { mapActions } from 'vuex';
    const GenericDlg = () => import('@/components/GenericDlg');

    export default {
        name: 'EmailDlg',

        components: {
            'progress-dlg': GenericDlg
        },

        props: {
            emailDlg: Boolean
        },

        data() {
            return {
                progressDlg: false,

                valid: true,
                name: '',
                email: '',
                emailRules: [
                    v => !!v || 'E-mail is required',
                    v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
                ],
                subject: '',
                subjectRules: [
                    v => (!v || v.length <= 256) || 'Subject must be less than 256 characters',
                ],
                message: ''
            }
        },

        computed: {
            mainDlg: {
                get: function () { return !this.progressDlg; },
                set: function () { this.$emit('done'); }
            }
        },

        watch: {
            emailDlg() {
                if (this.emailDlg === false)
                    this.$refs.emailForm.reset();
            }
        },

        methods: {
            ...mapActions('alert', [
                'displayAlert'
            ]),

            sendEmail() {

                let msg = {
                    name: this.name,
                    email: this.email,
                    subject: this.subject,
                    message: this.message
                };

                this.progressDlg = true;

                emailjs.send(
                    secrets.mail.service_id,
                    secrets.mail.template_id,
                    msg,
                    secrets.mail.user_id
                ).then( res => {
                    this.displayAlert({ message: res.text, type:'success' });
                    this.progressDlg = false;
                    this.$emit('done');
                }).catch( err => {
                    this.displayAlert({ message: err.text, type:'error' });
                    this.progressDlg = false;
                });
            }
        }

    }

</script>

<style>

    .email-subtitle {
        text-align: left;
    }

    .email-v-card {
        min-width: 600px;
    }

    @media only screen and (max-width: 960px) {
        .email-v-card {
            min-width: 360px;
        }
    }

</style>
